function parallax() {
    $('.page').tilt({
        perspective: 1800,
        easing: "cubic-bezier(.03,.98,.52,.99)",
        maxTilt: 20,
        speed: 2500,
        transition: true
    });
}

function transform($elements, scroll, blur) {
    $($elements).css({
        transform: '\n            perspective(50px),\n            translate3d(0px, 0px, ' + scroll + 'px)\n        '
    }).find('div > span').css({
        color: 'transparent',
        textShadow: '0 0 ' + blur + 'px white'
    });
}

function translateZTransition($elements, scroll) {
    $($elements).css({
        transform: '\n            perspective(50px),\n            translate3d(0px, 0px, ' + scroll + 'px)\n        '
    });
}

function blurTransition($elements, blur) {
    $($elements).find('div > span').css({
        color: 'transparent',
        textShadow: '0 0 ' + blur + 'px white'
    });
}

