function wheel() {

    //-- Variables for blur and scroll
    var scroll = 0;
    var scrollFactor = 0.5;
    var blur = 0;
    var blurFactor = 1.8;
    var scrollNumber = 0;
    var color = ['#832925', '#BC684F', '#9A615D', '#E2AAA5', '#E2A882', '#3A1D15'];

    //-- Pages
    var pages = $('.page > .content').children();
    console.log(pages);
    var actual = 1;
    var whileScroll = true;
    var checkScrollAfter = 150;
    var direction = 'up';
    var savedDirection = 'up';
    var savedStep = 0;
    var steps = 0;
    var onReset = false;
    var animationActive = false;
    var jumpFactor = 1.5; //-- Times middle animation is triggered
    var topScroll = 0; //-- Base case
    var topBlur = 0; //-- Base case
    var pageScrollFactor = 30;
    var pageBlurFactor = 20;

    window.addEventListener('wheel', function (e) {

        //-- Wheel down
        if (e.deltaY < 0) {
            scroll += scrollFactor;
            blur -= blurFactor;
            scrollNumber--;
            direction = 'down';
        }

        //-- Wheel up
        if (e.deltaY > 0) {
            scroll -= scrollFactor;
            blur += blurFactor;
            scrollNumber++;
            direction = 'up';
        }

        //-- Prevent default
        if (scroll > 0) {
            scroll = 0;
            blur = 0;
            scrollNumber = 0;
            onReset = true;
        }

        console.log("Scroll ", scrollNumber);

        //-- Transition if it is possible
        if (!animationActive) {
            Array.from(pages.slice(0, actual)).forEach(function (value, index) {
                //if (direction === 'up')
                $(value).customTransform(scroll + scrollFactor * pageScrollFactor * index, blur - blurFactor * pageBlurFactor * index);
                //else if (direction === 'down')
                //    $(value).customTransform(scroll - (scrollFactor * pageScrollFactor * index), blur + (blurFactor * pageBlurFactor * index));
            });
        }

        //-- Check movement steps 
        if (whileScroll) {
            whileScroll = false;

            setTimeout(function (lastDirection) {

                //-- Up direction
                if (lastDirection == 'up' && !animationActive) {
                    animationActive = true;

                    var scrollAnimationStates = {
                        initial: scroll,
                        final: topScroll
                    };

                    var blurAnimationStates = {
                        initial: blur,
                        final: topBlur
                    };

                    if (scrollNumber >= 3) {
                        //-- Scroll 3
                        console.log("Change page");

                        //-- Jump to the next page
                        topScroll = -(scrollFactor * pageScrollFactor);
                        topBlur = blurFactor * pageBlurFactor;
                        scrollAnimationStates.final = topScroll;
                        blurAnimationStates.final = topBlur;

                        //-- New page arrive animation
                        actual++;
                        actual = actual === 7 ? 6 : actual;
                        $(pages.get(actual - 1)).animate({ opacity: 1 }, 700);
                        $('body').attr('class', '');
                        $('body').addClass('color-' + actual);
                    }

                    Array.from(pages.slice(0, actual)).forEach(function (value, index) {
                        console.log("Tha index", {
                            from: scroll + scrollFactor * pageScrollFactor * index,
                            to: scrollAnimationStates.final + scrollFactor * pageScrollFactor * index
                        }, {
                            from: blurAnimationStates.initial - blurFactor * pageBlurFactor * index,
                            to: blurAnimationStates.final - blurFactor * pageBlurFactor * index
                        }, value);
                        $(value).specialAnimation({
                            from: scroll + scrollFactor * pageScrollFactor * index,
                            to: scrollAnimationStates.final + scrollFactor * pageScrollFactor * index
                        }, {
                            from: blurAnimationStates.initial - blurFactor * pageBlurFactor * index,
                            to: blurAnimationStates.final - blurFactor * pageBlurFactor * index
                        }).then(function (params) {
                            //-- Enable wheel movement again and positions righly set to the last step
                            animationActive = false;
                            whileScroll = true;
                            scrollNumber = 0;
                            scroll = topScroll;
                            blur = topBlur;
                        });
                    });
                } else if (lastDirection == 'down' && !animationActive) {
                    /*animate(
                        pages.slice(0, actual), 
                        {
                            initial: scroll,
                            final: scroll + scrollFactor * size,
                            factor: scrollFactor
                        },{
                            initial: blur,
                            final: blur - blurFactor * size,
                            factor: blurFactor
                        }, 'down').then((response) => {
                            //-- Animation ended
                            animationActive = false;
                        }).catch((error) => {
                            //-- Show error
                            console.log("An error ocurred :: ", error);
                        });*/
                }
            }, checkScrollAfter, direction);
        }
    });
}

